.project-page-title {
    font-family: 'Old Standard TT', serif;
    color: var(--greenColor);
    font-size: 72px;
    max-width: 780px;
    text-align: center;
}

.project-page-subtitle {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    margin-top: 40px;
    max-width: 780px;
    text-align: center;
}

.project-page-description {
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    margin-top: 35px;
    white-space: pre-line;
    max-width: 780px;
    text-align: center;
}

.project-section-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects-container {
    width: 100%;
    margin-bottom: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.project-section-title {
    font-family: var(--montserrat-font);
    font-weight: bold;
    font-size: 30px;
    color: var(--greenColor);
    margin-top: 40px;
    margin-bottom: 20px;
}
